.nav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__contents {
    display: flex;
    justify-content: space-between;
  }
  .nav__black {
      background-color: #111;
  }
  
  .nav__logo {
    position: fixed;
    top: 20px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
  }
  
  .nav__avatar {
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
  }
