.plansScreen__plan {
    display: flex;
    justify-content: space-between;
    padding:20px;
    opacity: 0.8;
}

.plansScreen__plan:hover {
    opacity:1;
}

.plansScreen__plan > button {
    font-size: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

